	module.exports = {"data":{"markdownRemark":{"html":"<h2>Storyboards</h2>\n<p>As a best practice, it’s the first sketch of the expected functionality of a project (e.g. a website, application, service, etc.). It has the purpose to aid in the planning of the requirements of the project and to outline the necessary steps (workflow) that completing a task requires. For instance, a sketch of an authentication web page lays out (before any code), what type of data is required from the user, what actions are necessary to perform in the back end, and what is the expected output of the action.</p>\n<h4>More Information:</h4>\n<p>Wikipedia. Storyboard <a href=\"https://en.wikipedia.org/wiki/Storyboard\">https://en.wikipedia.org/wiki/Storyboard</a></p>\n<p>Storyboards for Mobile Application.\n<a href=\"http://mobile-interaction-design.blogspot.com/2013/01/storyboards-for-mobile-application.html\">http://mobile-interaction-design.blogspot.com/2013/01/storyboards-for-mobile-application.html</a></p>","fields":{"slug":"/user-experience-research/storyboards/"},"frontmatter":{"title":"Storyboards"}}},"pathContext":{"slug":"/user-experience-research/storyboards/"}}

